import {
  colorFacetsBlue300,
  colorFacetsBlue500,
  colorFacetsBlue700,
  colorFacetsGold300,
  colorFacetsGold500,
  colorFacetsGreen300,
  colorFacetsGreen500,
  colorFacetsPink300,
  colorFacetsPink500,
  colorFacetsPurple400,
  colorFacetsPurple600,
  colorFacetsRed400,
  colorFacetsRed600,
  colorFacetsTeal200,
  colorFacetsTeal500,
  colorFacetsTeal700,
} from '@netlify/netlify-design-tokens';

const placeholderGradientColors = [
  { to: colorFacetsBlue700, from: colorFacetsBlue500 },
  { to: colorFacetsPink500, from: colorFacetsPink300 },
  { to: colorFacetsPurple600, from: colorFacetsPurple400 },
  { to: colorFacetsGreen500, from: colorFacetsGreen300 },
  { to: colorFacetsRed600, from: colorFacetsRed400 },
  { to: colorFacetsTeal500, from: colorFacetsTeal200 },
  { to: colorFacetsGold500, from: colorFacetsGold300 },
  { to: colorFacetsBlue500, from: colorFacetsBlue300 },
  { to: colorFacetsTeal700, from: colorFacetsTeal500 },
];

export const getPlaceholderGradientColorsFromId = (id = '') => {
  if (!id || typeof id !== 'string') {
    return placeholderGradientColors[0];
  }

  // We don't fully randomise the color selection; in some cases like Organizations/Teams, there may be more than one placeholder instance
  // across the app, and we want them all to use a consistent color. We instead use the item's ID as the basis for color selection.
  const idCharactersAsNumbers = id
    .split('')
    .map((char) => Number(char))
    .reverse();

  const maxIndex = placeholderGradientColors.length - 1;
  const idInferredIndex =
    idCharactersAsNumbers.find(
      (charAsNum) => !isNaN(charAsNum) && charAsNum <= maxIndex
    ) ?? 0;

  return placeholderGradientColors[idInferredIndex];
};
